  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-style: normal;
  font-weight: 700; }

@font-face {
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-style: normal;
  font-weight: 400; }

html {
  height: 100%;
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

body {
  font-size: 16px;
  line-height: 21.5px;
  font-weight: 400;
  min-width: 320px;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: "PT Mono", monospace;
  color: #666666;
  background-color: #ffffff; }

.content {
  flex: 1 1 auto; }

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  overflow: hidden; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  white-space: nowrap; }

.page-header__body {
  display: flex;
  justify-content: space-between;
  align-self: center;
  max-width: 1440px;
  padding: 33px 14px;
  margin: 0 auto;
  background-color: #1B2D37; }

