  position: relative;
  display: flex;
  justify-content: space-between;
  align-self: center;
  width: 202px; }
  .page-header__logo::after {
    content: "";
    position: absolute;
    top: 35%;
    right: -154px;
    width: 37px;
    height: 1px;
    background-color: #ffffff;
    box-shadow: 0 12px 0 0 #ffffff; }

.page-header__logo-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 9px; }

.page-header__info {
  padding: 0;
  margin: 0;
