  justify-content: space-between;
  align-items: center;
  margin-left: auto; }

.page-header__info a {
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
  cursor: pointer; }

.page-header__item:hover {
  opacity: 0.7; }

.page-header__item--email {
  margin-right: 88px; }

.page-header__item--email svg {
  vertical-align: middle;
  margin-right: 11px; }

.page-header__item--phone svg {
  vertical-align: middle;
  margin-right: 5px; }

.page-header__btn {
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  display: inline-block;
  align-self: center;
  padding: 14px 32px;
  margin-left: 51px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer; }
  .page-header__btn:hover {
    background-color: #00b2ff; }

@media (max-width: 1023px) {
