    padding: 20px 25px; }
  .page-header__logo::after {
    right: -400px; }
  .page-header__info {
    width: 83px; }
  .page-header__info span {
    display: none; }
  .page-header__item--email {
    margin-right: 0; }
  .page-header__item--email svg {
