  .page-header__item--phone svg {
    margin-right: 0;
    width: 24px;
    height: 24px; }
  .page-header__btn {
    display: none; } }

@media (max-width: 767px) {
  .page-header__body {
    padding: 14px 15px; }
  .page-header__logo {
    width: 38px; }
    .page-header__logo::after {
      right: -113px; }
  .page-header__logo-text {
    display: none; }
  .page-header__info {
    width: 73px; } }

.page-footer__body {
  display: flex;
